<template>
<div class="tusk">
  <ul>
      <Tusk
      v-for="(tusk, i) of tusks"
      v-bind:tusk="tusk"
      v-bind:index="i"
      v-on:remove-tusk="removeTusk"
       />
  </ul>
</div>
</template>

<script>
import Tusk from '@/components/Tusks/Tusk'
export default {
  props: ['tusks'],
  components:{
    Tusk
  },
  methods:{
    removeTusk(id){
      this.$emit('remove-tusk', id)
    }
  }
}
</script>

<style scoped>
  ul{
    list-style: none;
    margin: 1rem;
    padding: 0;
  }

</style>
