<template>
  <li>
    <span v-bind:class="{done: tusk.completed}">
      <input type="checkbox"
       v-on:change="tusk.completed=!tusk.completed">
      <strong>{{index + 1}}</strong>
      {{tusk.tusktext}}
    </span>
    <button class="bt" v-on:click="$emit('remove-tusk', tusk.id)"
    >&times;</button>
  </li>
</template>

<script>
export default {
  props: {
    tusk:{
      type: Object,
      required: true
    },
    index: Number
  }
}
</script>

<style  scoped>
li{
  border: 1px solid #ccc;
  display: flex;
  justify-content: space-between;
  padding: .5rem 2rem;
  margin-bottom: 1rem;
}

.bt{
  background: red;
  color:#fff;
  border-radius: 50%;
  font-weight: bold;
}

input{
  margin-right: 1rem;
}

.done{
  text-decoration: line-through;
}
</style>
