<template>
  <li>
    <span v-bind:class="{done: rule.completed}">
      <input type="checkbox"
       v-on:change="rule.completed=!rule.completed">
      <strong>{{index + 1}}</strong>
      {{rule.ruletext}}
    </span>
    <button class="bt" v-on:click="$emit('remove-rule', rule.id)"
    >&times;</button>
  </li>
</template>

<script>
export default {
  props: {
    rule:{
      type: Object,
      required: true
    },
    index: Number
  }
}
</script>

<style  scoped>
  li{
    border: 1px solid #ccc;
    display: flex;
    justify-content: space-between;
    padding: .5rem 2rem;
    margin-bottom: 1rem;
  }

  .bt{
    background: red;
    color:#fff;
    border-radius: 50%;
    font-weight: bold;
  }

  input{
    margin-right: 1rem;
  }

  .done{
    text-decoration: line-through;
  }
</style>
