<template>
  <form @submit.prevent="OnSubmit">
    <input type="text" v-model="ruletext">
    <button type="submit">Добавить задачу</button>
  </form>
</template>
<script>
  export default{
    data(){
      return{
        ruletext: ''
      }
    },
    methods:{
      OnSubmit(){
        console.log('Submit', this.ruletext)
        if (this.ruletext.trim()){
          const newRule = {
            id: Date.now(),
            ruletext: this.ruletext,
            completed: false
          }

          this.$emit('add-rule', newRule)
          this.ruletext = ''
        }
      }
    }
  }
</script>

<style scoped>
  form{
    margin: 1rem;
    padding: 1rem;
    display: flex;
  }
  input{
    background: #ffe3d8;
    margin-right: 1em;
    width: 20em;
  }
  button{
    background: green;
    color: white;
  }
  button:hover{
    background: white;
    color: green;
    transition: background .5s;
  }
</style>
