<template>
  <div id="app">
      <div class = "left-bar">
          <div><img id = "logo" src="@/assets/logo.jpg"></div>
          <LeftSidebar/>
      </div>
      <div class = "right-window">
        <div class = "main">

        <div class = "left">
          <div class = "Rules">
            <AddRule
              @add-rule="addRule"
            />
            <Rules
              v-bind:rules="rules"
              @remove-rule="removeRule"
            />
          </div>
          <hr>
          <div class="Tusks">
            <AddTusk
              @add-tusk="addTusk"
            />
            <Tusks
            v-bind:tusks="tusks"
            @remove-tusk="removeTusk"
            />
          </div>
        </div>

        <div class = "right">
          ЗДЕСЬ <br> ДОЛЖЕН БЫТЬ <br> ЧАТ
        </div>

        </div>
        <div class = "footer">
          СОСТОЯНИЕ
        </div>
      </div>
  </div>
</template>

<script>
import Rules from '@/components/Rules/Rules'
import AddRule from '@/components/Rules/AddRule'
import Tusks from '@/components/Tusks/Tusks'
import AddTusk from '@/components/Tusks/AddTusk'
import LeftSidebar from '@/components/LeftSidebar'

export default {
  name: 'app',
  data(){
    return{
      rules:[
        {id: 1, ruletext: "Здесь находится текст задач", completed: false},
      ],
      tusks:[
        {id: 1, tusktext: "Здесь находится текст правил", completed: false},
      ]
    }
  },
  methods:{
    removeRule(id){
      this.rules = this.rules.filter(t => t.id !== id)
    },
    addRule(rule){
      this.rules.push(rule)
    },
    removeTusk(id){
      this.tusks = this.tusks.filter(t => t.id !== id)
    },
    addTusk(tusk){
      this.tusks.push(tusk)
    },
  },
  components: {
    LeftSidebar,
    Rules,
    Tusks,
    AddRule,
    AddTusk
  }
}
</script>
<style>
    #app {
        overflow: auto;
        height: 100%;
        display: flex;
        background: linear-gradient(to bottom, #DF0001, #FFE5A9);
    }
    #logo{
        width: 100%;
    }
    .left-bar{
        display: flex;
        flex-direction: column;
        margin: 1em;
        width:30%;
    }
    .right-window{
      width:100%;
      display: flex;
      flex-direction: column;
    }
    .main{
      display: flex;
        background: #ffe3d8;
        border: 1px solid black;
        margin: 1em 1em 0 0;
        flex: 1 0 auto;
    }
    .left{
      width: 70%;
    }
    .right{
      text-align: center;
      width:50%;
    }
    .footer{
      display: flex;
      flex:0 0 auto;
        margin: 1em 1em 1em 0;
        background: #FFB4AF;
        border: 1px solid black;
    }

    .Rules{
      box-sizing: border-box;
      flex-direction: column;
      display: flex;
      max-height: 40%;
      overflow-y: auto;
    }

    .Tusks{
      box-sizing: border-box;
      flex-direction: column;
      display: flex;
      max-height: 40%;
      overflow-y: auto;
    }
</style>
