<template>
<div class="rule">
  <ul>
      <Rule
      v-for="(rule, i) of rules"
      v-bind:rule="rule"
      v-bind:index="i"
      v-on:remove-rule="removeRule"
       />
  </ul>
</div>
</template>

<script>
import Rule from '@/components/Rules/Rule'
export default {
  props: ['rules'],
  components:{
    Rule
  },
  methods:{
    removeRule(id){
      this.$emit('remove-rule', id)
    }
  }
}
</script>

<style scoped>
  ul{
    list-style: none;
    margin: 1rem;
    padding: 0;
  }

</style>
