<template>
  <form @submit.prevent="OnSubmit1">
    <input type="text" v-model="tusktext">
    <button type="submit">Добавить правило</button>
  </form>
</template>
<script>
  export default{
    data(){
      return{
        tusktext: ''
      }
    },
    methods:{
      OnSubmit1(){
        console.log('Submit', this.tusktext)
        if (this.tusktext.trim()){
          const newTusk = {
            id: Date.now(),
            tusktext: this.tusktext,
            completed: false
          }

          this.$emit('add-tusk', newTusk)
          this.tusktext = ''
        }
      }
    }
  }
</script>

<style scoped>
  form{
    margin: 1rem;
    padding: 1rem;
    display: flex;
  }
  input{
    background: #ffe3d8;
    margin-right: 1em;
    width: 20em;
  }
  button{
    background: green;
    color: white;
  }
  button:hover{
    background: white;
    color: green;
    transition: background .5s;
  }
</style>
